import React from 'react'
import Footer from '../footer/Footer';


import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import Carousel from 'react-bootstrap/Carousel';
const Pando = () => {
  const videoId = 'uGtACQ0d4fg';
  return (
    <div>
         <div style={{ height: '100%', overflow: 'hidden' }}>
          <Container fluid className="h-100">
            <Row className="h-100">
              <Col xs={12} md={8} lg={6} className="mx-auto my-auto">
                <Carousel className="w-100 h-100"> 
                  <Carousel.Item>
                    <img src={"./imagenes/pando1.jpg"} alt="Plato Paceo" className="img-fluid h-100 w-100" />
                  </Carousel.Item>
                  <Carousel.Item>
                    <img src={"./imagenes/pando2.jpg"} alt="Plato Paceo" className="img-fluid h-100 w-100" />
                  </Carousel.Item>
                  <Carousel.Item>
                    <img src={"./imagenes/pando3.jpg"} alt="Plato Paceo" className="img-fluid h-100 w-100" />
                  </Carousel.Item>
                </Carousel>
              </Col>
            </Row>
          </Container>
        </div>
        <h1 className='text-white p-2 bg-dark'>PANDO</h1>

        <blockquote className='blockquote'>
                <p className='text.center mt-3'>
                Los Platos tpicos del departamento de Pando son la Farofa, Escabeche de Torcaza, Carne de Moroco, Sudado de Surub entre otros que caractersticos de esta regin oriental de Bolivia, elaborado con productos de los ros y los montes.
                </p>
            </blockquote>
            <h1 className='text-white p-2 bg-dark'>ESCABECHE DE TORCAZA</h1>
            <img src="./imagenes/ESCABECHE.jpg" width= '80%' height= '315' alt="kalapurka" fluid />
            <p className='text.center mt-3'>
                    <h2>Ingredientes</h2>
                    <h3>8 o 10 torcazas</h3>
                    <h3>4 cebollas medianas</h3>
                    <h3>2 hojas de laurel</h3>
                    <h3>4 zanahorias 1/2 taza de aceite 1  taza de vino blanco</h3>
                    <h3>2 cucharones de caldo</h3>
                    <h3>2 locotos</h3>
                    <h3>Sal y pimienta al gusto</h3>

                    <h1>Paso a paso</h1>

                    <h3>Paso 1 
                    Lavar bien las torcazas y dividir cada una en dos partes. Poner en una cacerola el aceite, cebolla cortada, zanahorias cortadas en rajitas, vino o vinagre, y el caldo y las torcazas. Haga cocer en fuego lento hasta que las torcazas estn tiernas.</h3>
                    <h3>Paso 2
                    Si es necesario. Agregue ms caldo.</h3>
                    <h3>Paso 3
                    Cuando est listo, ponga el preparado en una fuente, dejar enfriar y lleve a la heladera hasta el da siguiente.</h3>
                    
            </p>
            
            <h1 className='text-white p-2 bg-dark'>VIDEO PREPARACION</h1>
            <Container>
                    <Row>
                        <Col md={6} className="mx-auto mt-5">
                            <div className="embed-responsive embed-responsive-16by9">
                                <iframe
                                    width='80%'
                                    height='315'
                                    title="YouTube Video"
                                    className="embed-responsive-item"
                                    src={`https://www.youtube.com/embed/${videoId}`}
                                    allowFullScreen />
                            </div>
                        </Col>
                    </Row>
            </Container>


        <Footer/>
    </div>
  )
}

export default Pando