import React from 'react'
import Footer from '../footer/Footer';

import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import Carousel from 'react-bootstrap/Carousel';

const SantaCruz = () => {
  const videoId = 'wbOBTLZ_0uc';
  return (
    <div>
        <div style={{ height: '100%', overflow: 'hidden' }}>
          <Container fluid className="h-100">
            <Row className="h-100">
              <Col xs={12} md={8} lg={6} className="mx-auto my-auto">
                <Carousel className="w-100 h-100"> 
                  <Carousel.Item>
                    <img src={"./imagenes/santacruz1.jpg"} alt="Plato Paceo" className="img-fluid h-100 w-100" />
                  </Carousel.Item>
                  <Carousel.Item>
                    <img src={"./imagenes/santacruz2.jpg"} alt="Plato Paceo" className="img-fluid h-100 w-100" />
                  </Carousel.Item>
                  <Carousel.Item>
                    <img src={"./imagenes/santacruz3.jpg"} alt="Plato Paceo" className="img-fluid h-100 w-100" />
                  </Carousel.Item>
                </Carousel>
              </Col>
            </Row>
          </Container>
        </div>
        <h1 className='text-white p-2 bg-dark'>SANTA CRUZ</h1>
        

        <h1 className='text-white p-2 bg-dark'>LA PAZ</h1>
            <blockquote className='blockquote'>
                <p className='text.center mt-3'>
                Los platos tpicos de Santa Cruz Bolivia son el Majao, locro de gallina, el zonzo entre otros, tambin son tradicional degustar cuap y pan de de arroz. 
                </p>
            </blockquote>
            <h1 className='text-white p-2 bg-dark'>MAJADITO CRUCEO</h1>
            <img src="./imagenes/majadito.jpg" width= '80%' height= '315' alt="Plato Paceo" fluid />
            <p className='text.center mt-3'>
                    <h2>Ingredientes</h2>
                    <h3>1 cebolla</h3>
                    <h3>1 diente de ajo</h3>
                    <h3>1 sal al gusto</h3>
                    <h3>4 huevos</h3>
                    <h3>1 aceite (el necesario)</h3>
                    <h3> 1/2 pimentn</h3>
                    <h3> 1/2 tomate</h3>
                    <h3> 1/4 libra de chorizo para frer</h3>
                    <h3>1 1/2 cucharadas de azafrn</h3>
                    <h3>1 1/2 tazas de arroz</h3>
                    <h3>1 libra de yuca</h3>
                    <h3>2 pltanos maduros</h3>
                    <h3>1 libra de charque</h3>

                    <h1>Paso a paso</h1>

                    <h3>Paso 1
                    Remoja el charque en agua tibia durante quince minutos, pare quitarle la sal, luego mortaja cada lonja hasta que pueda ser desmenuzado con los dedos. Tustalo en un poco aceite y retira cuando estn crocantes.</h3>
                    <h3>Paso 2
                    Lava el arroz y en una sartn grande, pngalo a cocinar sin agua removiendo constantemente hasta que seque el arroz, aade un diente de ajo finamente picado, y sigue removiendo hasta que se encuentre dorado.</h3>
                    <h3>Paso 3
                    Luego, aade la cabeza de cebolla, el tomate y el pimentn finamente picados, agrega el azafrn y el charque removiendo por unos minutos ms.</h3>
                    <h3>Paso 4 En una olla agrega el arroz, vierte tres tazas de agua caliente, aade poca sal y cocina a fuego alto. Cuando el agua se vaya consumiendo, disminuye el fuego a bajo.</h3>
                    <h3>Paso 5 Aparte, pon a cocer la yuca pelada en agua caliente con sal. En una sartn, fre en bastante aceite caliente, los pltanos cortados en tajadas, y los chorizos cortados en rodajas. Mezcla los chorizos con el arroz. </h3>
                    <h3>Paso 6 Sirve el majao, en cada plato pon varias tajadas de pltano, y un huevo frito encima del arroz. En una fuente colocar las yucas.</h3>
                    
            </p>
            
            <h1 className='text-white p-2 bg-dark'>VIDEO PREPARACION</h1>
            <Container>
                    <Row>
                        <Col md={6} className="mx-auto mt-5">
                            <div className="embed-responsive embed-responsive-16by9">
                                <iframe
                                    width='80%'
                                    height='315'
                                    title="YouTube Video"
                                    className="embed-responsive-item"
                                    src={`https://www.youtube.com/embed/${videoId}`}
                                    allowFullScreen />
                            </div>
                        </Col>
                    </Row>
            </Container>

        <Footer/>
    </div>
  )
}

export default SantaCruz