import React from 'react'
import Footer from '../footer/Footer';

import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import Carousel from 'react-bootstrap/Carousel';
const Sucre = () => {
  const videoId = 'fPT-Fyqu-xc';
  return (
    <div>

        <div style={{ height: '100%', overflow: 'hidden' }}>
          <Container fluid className="h-100">
            <Row className="h-100">
              <Col xs={12} md={8} lg={6} className="mx-auto my-auto">
                <Carousel className="w-100 h-100"> 
                  <Carousel.Item>
                    <img src={"./imagenes/sucre1.jpg"} alt="Plato Paceo" className="img-fluid h-100 w-100" />
                  </Carousel.Item>
                  <Carousel.Item>
                    <img src={"./imagenes/sucre2.jpg"} alt="Plato Paceo" className="img-fluid h-100 w-100" />
                  </Carousel.Item>
                  <Carousel.Item>
                    <img src={"./imagenes/sucre3.jpg"} alt="Plato Paceo" className="img-fluid h-100 w-100" />
                  </Carousel.Item>
                </Carousel>
              </Col>
            </Row>
          </Container>
        </div>

        
        <h1 className='text-white p-2 bg-dark'>SUCRE</h1>
            <blockquote className='blockquote'>
                <p className='text.center mt-3'>
                Tarija ofrece a los visitantes toda una variedad de platos tpicos de culinaria y repostera tarijea. El saice, la ranga ranga, el chancao y los picantes abundan porque son parte de la gastronoma tarijea, lo mismo que los panecillos, rosquetes y masitas, sin mencionar las deliciosas bebidas del lugar, como el conocido vino patero.
                </p>
            </blockquote>
            <h1 className='text-white p-2 bg-dark'>FRITANGA</h1>
            <img src="./imagenes/FRITANGA.jpg" width= '80%' height= '315' alt="Plato Paceo" fluid />
            <p className='text.center mt-3'>
                    <h2>Ingredientes</h2>
                    <h3>3 kilogramos de Carne de cerdo</h3>
                    <h3> libra de Chuo remojado</h3>
                    <h3>1 libra de Patasca</h3>
                    <h3> 1 libra de Papas</h3>
                    <h3>  libra de Aj colorado molido</h3>
                    <h3>2 cucharillas de Pimienta</h3>
                    <h3> 1 cucharilla de Comino</h3>
                    <h3> 1 Cabeza de ajo</h3>

                    <h1>Paso a paso</h1>

                    <h3>Paso 1
                    Troce la carne de cerdo y cocine en agua con pimienta, comino, ajos molidos y sal. Cuando la carne est suave, escurra y fra.</h3>
                    <h3>Paso 2
                    Aparte mezcle 2 tazas de caldo donde coci el cerdo con aj colorado, prepare una salsa e incorpore el cerdo frito.</h3>
                    <h3>Paso 3
                    Deje cocer hasta que espese.</h3>
                    <h3>Paso 4 Pele, cocine las papas</h3>
                    <h3>Paso 5 En agua hirviendo con sal a gusto ponga a cocer el chuo.</h3>
                    <h3>Paso 6Sirva la fritanga con atasca, papas y chuo</h3>
                    
            </p>
            
            <h1 className='text-white p-2 bg-dark'>VIDEO PREPARACION</h1>
            <Container>
                    <Row>
                        <Col md={6} className="mx-auto mt-5">
                            <div className="embed-responsive embed-responsive-16by9">
                                <iframe
                                    width='80%'
                                    height='315'
                                    title="YouTube Video"
                                    className="embed-responsive-item"
                                    src={`https://www.youtube.com/embed/${videoId}`}
                                    allowFullScreen />
                            </div>
                        </Col>
                    </Row>
            </Container>
        
        

        <Footer/>
    </div>
  )
}

export default Sucre