import React from 'react'
import Footer from '../footer/Footer';

import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import Carousel from 'react-bootstrap/Carousel';

const Tarija = () => {
  const videoId = 'op33su0Q-lg';
  return (
    <div>
      
        <div style={{ height: '100%', overflow: 'hidden' }}>
          <Container fluid className="h-100">
            <Row className="h-100">
              <Col xs={12} md={8} lg={6} className="mx-auto my-auto">
                <Carousel className="w-100 h-100"> 
                  <Carousel.Item>
                    <img src={"./imagenes/tarija1.jpg"} alt="Plato Paceo" className="img-fluid h-100 w-100" />
                  </Carousel.Item>
                  <Carousel.Item>
                    <img src={"./imagenes/tarija2.jpg"} alt="Plato Paceo" className="img-fluid h-100 w-100" />
                  </Carousel.Item>
                  <Carousel.Item>
                    <img src={"./imagenes/tarija3.jpg"} alt="Plato Paceo" className="img-fluid h-100 w-100" />
                  </Carousel.Item>
                </Carousel>
              </Col>
            </Row>
          </Container>


          <h1 className='text-white p-2 bg-dark'>TARIJA</h1>
            <blockquote className='blockquote'>
                <p className='text.center mt-3'>
                Tarija ofrece a los visitantes toda una variedad de platos tpicos de culinaria y repostera tarijea. El saice, la ranga ranga, el chancao y los picantes abundan porque son parte de la gastronoma tarijea, lo mismo que los panecillos, rosquetes y masitas, sin mencionar las deliciosas bebidas del lugar, como el conocido vino patero.
                </p>
            </blockquote>
            <h1 className='text-white p-2 bg-dark'>SAICE TARIJEO</h1>
            <img src="./imagenes/InicioTarija.jpg" width= '80%' height= '315' alt="Plato Paceo" fluid />
            <p className='text.center mt-3'>
                    <h2>Ingredientes</h2>
                    <h3>1 kg de carne blanda de res</h3>
                    <h3>1/2 kg de pata de res</h3>
                    <h3> 4 papas grandes</h3>
                    <h3> 3 cebollas medianas</h3>
                    <h3> 1 tomate grande</h3>
                    <h3> 1 taza de naranja</h3>
                    <h3> Sal y pimienta a gusto</h3>
                    <h3> 6 vainas de aj colorado molido</h3>
                    <h3> 1 cucharilla de pimienta en grano </h3>
                    <h3> 1 cucharilla de comino en grano</h3>
                    <h3>3 dientes de ajo picado</h3>

                    <h1>Paso a paso</h1>

                    <h3>Paso 1
                    Cocer las patas hasta hacer caldo.</h3>
                    <h3>Paso 2
                    Picar en dados fino la cebolla, el ajo y saltear con un poco de aceite, las vainas molidas en forma de pur aada a la olla, condimentar con sal, pimienta y comino recin molido, deje cocinar durante 20 minutos con cuatro tazas de caldo de pata.</h3>
                    <h3>Paso 3
                    Luego picar la carne y poner al caldo de pata con los dems ingredientes y por ltimo agregar las arvejas precocidas,</h3>
                    <h3>Paso 4 como entrada tambin puede preparar una deliciosa sopa de mani chapaco.   y para darle mas sabor agregarlo llajwa</h3>
                    
            </p>
            
            <h1 className='text-white p-2 bg-dark'>VIDEO PREPARACION</h1>
            <Container>
                    <Row>
                        <Col md={6} className="mx-auto mt-5">
                            <div className="embed-responsive embed-responsive-16by9">
                                <iframe
                                    width='80%'
                                    height='315'
                                    title="YouTube Video"
                                    className="embed-responsive-item"
                                    src={`https://www.youtube.com/embed/${videoId}`}
                                    allowFullScreen />
                            </div>
                        </Col>
                    </Row>
            </Container>
        </div>
        



        <Footer/>
    </div>
  )
}

export default Tarija