import React from 'react'
import {Link} from 'react-router-dom'

const navbar = () => {
  return (
    <div>
        <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
            <div className="container-fluid">
                <Link to='/'>
                    <img src='./imagenes/logoAlimento.png' width='50'/>
                </Link>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarNav">
                    <ul className="navbar-nav mx-auto">
                        <li className="nav-item">
                            <Link className="nav-link" to= '/'>Inicio</Link>
                        </li>
                        
                        <li className="nav-item">
                            <Link className="nav-link" to= '/LaPaz'>La Paz</Link>
                        </li>

                        <li className="nav-item">
                            <Link className="nav-link" to= '/Oruro'>Oruro</Link>
                        </li>

                        <li className="nav-item">
                            <Link className="nav-link" to= '/Cochabamba'>Cochabamba</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to= '/SantaCruz'>Santa Cruz</Link>
                        </li>

                        <li className="nav-item">
                            <Link className="nav-link" to= '/Pando'>Pando</Link>
                        </li>

                        <li className="nav-item">
                            <Link className="nav-link" to= '/Beni'>Beni</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to= '/Potosi'>Potosi</Link>
                        </li>

                        <li className="nav-item">
                            <Link className="nav-link" to= '/Sucre'>Chuquisaca</Link>
                        </li>

                        <li className="nav-item">
                            <Link className="nav-link" to= '/Tarija'>Tarija</Link>
                        </li>

                        <li className="nav-item">
                            <Link className="nav-link" to= '/Credito'>Credito</Link>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>
    </div>
  )
}

export default navbar