import React from 'react'
import Image from 'react-bootstrap/Image';
import { Container, Row, Col } from 'react-bootstrap';
import Footer from '../footer/Footer';

import Button from 'react-bootstrap/Button';
import Carousel from 'react-bootstrap/Carousel';


const LaPaz = () => {
    const videoId = 'Q7cyBFPwqB0';
    return (
        <div>

            <div style={{ height: '100%', overflow: 'hidden' }}>
            <Container fluid className="h-100">
                <Row className="h-100">
                <Col xs={12} md={8} lg={6} className="mx-auto my-auto">
                    <Carousel className="w-100 h-100"> 
                    <Carousel.Item>
                        <img src={"./imagenes/La Paz1.jpg"} alt="Plato Paceo" className="img-fluid h-100 w-100" />
                    </Carousel.Item>
                    <Carousel.Item>
                        <img src={"./imagenes/La Paz2.jpg"} alt="Plato Paceo" className="img-fluid h-100 w-100" />
                    </Carousel.Item>
                    <Carousel.Item>
                        <img src={"./imagenes/La Paz3.jpg"} alt="Plato Paceo" className="img-fluid h-100 w-100" />
                    </Carousel.Item>
                    </Carousel>
                </Col>
                </Row>
            </Container>
            </div>


            <h1 className='text-white p-2 bg-dark'>LA PAZ</h1>
            <blockquote className='blockquote'>
                <p className='text.center mt-3'>
                    Embrcate en un delicioso viaje por la gastronoma pacea. Descubre los secretos detrs de los platos paceos ms amados, disfruta de nuestra comida tpica y revive las tradiciones a travs de nuestra comida tradicional.
                </p>

                <p className='text.center mt-3'>
                    Desde el plato tpico de La Paz, que evoca la herencia y las tradiciones de nuestra tierra, hasta las innovaciones culinarias modernas que se inspiran en nuestras races; "Sabores Paceos" se dedica a explorar y celebrar cada sabroso bocado de nuestra comida pacea.
                </p>
            </blockquote>
            <h1 className='text-white p-2 bg-dark'>PLATO PACEO</h1>
            <img src="./plato paceo.jpg" width= '80%' height= '315' alt="Plato Paceo" fluid />
            <p className='text.center mt-3'>
                    <h2>Ingredientes</h2>
                    <h3>5 libras haba con cscara (para tres platos)</h3>
                    <h3>1/2 kg. De carne (bife, pulpa, bistec)</h3>
                    <h3>6 papas (para tres platos)</h3>
                    <h3>6 choclos</h3>
                    <h3>1 queso</h3>
                    <h3>1 botellita soya</h3>
                    <h2>Para la llajua (aji picante) :</h2>
                    <h3>1/2 libra tomate</h3>
                    <h3>2 locotos </h3>

                    <h1>Paso a paso</h1>

                    <h3>Paso 1
Lavar bien la haba sin pelar y sin sacar de la cscara y colocar en una olla con agua y hacer hervir (mejor si es en olla a presin)</h3>
                    <h3>Paso 2
Lavar la papa y no pelar, mejor si es pap con cscara roja, colocar en una olla con agua (mejor si es en olla a presin)</h3>
                    <h3>Paso 3
Para el choclo o maz , en una olla mejor si es a presin, 2 litros de agua y agregar azcar, 2 cucharas y media por litro de agua. Una vez que el agua est hirviendo recin colocar el maz o choclo, y hacer cocer por lo menos 40 minutos</h3>
                    <h3>Paso 4
Para la carne, yo lo ablando con papaya, colocando una capa de papaya y luego al sartn con salsa soya, pimienta, y comino al gusto, cocinar a fuego lento o mnimo para que salga jugo de la carne</h3>
                    <h3>Paso 5
Con el mismo jugo de la carne, cocinar dos tiras de queso </h3>
            </p>
            
            <h1 className='text-white p-2 bg-dark'>VIDEO PREPARACION</h1>
            <Container>
                    <Row>
                        <Col md={6} className="mx-auto mt-5">
                            <div className="embed-responsive embed-responsive-16by9">
                                <iframe
                                    width='80%'
                                    height='315'
                                    title="YouTube Video"
                                    className="embed-responsive-item"
                                    src={`https://www.youtube.com/embed/${videoId}`}
                                    allowFullScreen />
                            </div>
                        </Col>
                    </Row>
            </Container>

            <Footer/>

            </div>
                
  )
}

export default LaPaz