import React from 'react'
import Footer from '../footer/Footer';

import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import Carousel from 'react-bootstrap/Carousel';

const Beni = () => {
  const videoId = 'K-v1XRtoLrY';
  return (
    <div>
          <div style={{ height: '100%', overflow: 'hidden' }}>
          <Container fluid className="h-100">
            <Row className="h-100">
              <Col xs={12} md={8} lg={6} className="mx-auto my-auto">
                <Carousel className="w-100 h-100"> 
                  <Carousel.Item>
                    <img src={"./imagenes/Beni1.jpg"} alt="Plato Paceo" className="img-fluid h-100 w-100" />
                  </Carousel.Item>
                  <Carousel.Item>
                    <img src={"./imagenes/Beni2.jpg"} alt="Plato Paceo" className="img-fluid h-100 w-100" />
                  </Carousel.Item>
                  <Carousel.Item>
                    <img src={"./imagenes/Beni3.jpg"} alt="Plato Paceo" className="img-fluid h-100 w-100" />
                  </Carousel.Item>
                </Carousel>
              </Col>
            </Row>
          </Container>
        </div>
        <h1 className='text-white p-2 bg-dark'>BENI</h1>


        <blockquote className='blockquote'>
                <p className='text.center mt-3'>
                Es tpico de la cocina beniana el majadito de yuca, el charque con chiv, la sopa de chipilo, las ensaladas de papaya verde con cebolla en hoja o la ensalada de palmito, el masaco de pltano maduro, el tamal a la olla o al horno, el payuje, el pan de arroz, entre algunos de los ms caractersticos, sin mencionar la diversidad de bebidas preparadas con frutas tropicales.
                </p>
            </blockquote>
            <h1 className='text-white p-2 bg-dark'>MASACO</h1>
            <img src="./imagenes/InicioCochabama.jpg" width= '80%' height= '315' alt="Plato Paceo" fluid />
            <p className='text.center mt-3'>
                    <h2>Ingredientes</h2>
                    <h3>6 pltanos</h3>
                    <h3> kg queso</h3>
                    <h3> 1 cebolla</h3>
                    <h3> Aceite</h3>
                    <h3>1 SAL</h3>
                    

                    <h1>Paso a paso</h1>

                    <h3>Paso 1
                    Pela el pltano y crtalo en crculo.</h3>
                    <h3>Paso 2
                    Frer en un sarten con un poco de aceite hasta que queden amarillos</h3>
                    <h3>Paso 3
                    luego llevar a un tac (Este es un hueco de madero como un mortero) y comenzar a destrozar el pltano frito, caso de no tener tac utilizar un martillo de cocina  en una bandeja.</h3>
                    <h3>Paso 4
                    Una vez que el pltano est molido agregar queso rallado</h3>
                    <h3>Paso 5
                    Picar la cebolla, agregar un poco de aceite y sofrer y luego mezcle con queso y el resto de la masa.</h3>
                    <h3>Paso 6 
                    luego puede prepararlo como panecillo para poder servir en cada plato
el masaco se acompaa con CAFE Coroico lo boliviano es mas rico.</h3>
                    
            </p>
            
            <h1 className='text-white p-2 bg-dark'>VIDEO PREPARACION</h1>
            <Container>
                    <Row>
                        <Col md={6} className="mx-auto mt-5">
                            <div className="embed-responsive embed-responsive-16by9">
                                <iframe
                                    width='80%'
                                    height='315'
                                    title="YouTube Video"
                                    className="embed-responsive-item"
                                    src={`https://www.youtube.com/embed/${videoId}`}
                                    allowFullScreen />
                            </div>
                        </Col>
                    </Row>
            </Container>





        <Footer/>
    </div>
  )
}

export default Beni