import React from 'react'
import Footer from '../footer/Footer';

import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import Carousel from 'react-bootstrap/Carousel';
const Potosi = () => {
  const videoId = 'F0tNbt-VLoY';
  return (
    <div>
        <div style={{ height: '100%', overflow: 'hidden' }}>
          <Container fluid className="h-100">
            <Row className="h-100">
              <Col xs={12} md={8} lg={6} className="mx-auto my-auto">
                <Carousel className="w-100 h-100"> 
                  <Carousel.Item>
                    <img src={"./imagenes/potosi1.jpg"} alt="Plato Paceo" className="img-fluid h-100 w-100" />
                  </Carousel.Item>
                  <Carousel.Item>
                    <img src={"./imagenes/potosi2.jpg"} alt="Plato Paceo" className="img-fluid h-100 w-100" />
                  </Carousel.Item>
                  <Carousel.Item>
                    <img src={"./imagenes/potosi3.jpg"} alt="Plato Paceo" className="img-fluid h-100 w-100" />
                  </Carousel.Item>
                </Carousel>
              </Col>
            </Row>
          </Container>
        </div>
        <h1 className='text-white p-2 bg-dark'>POTOSI</h1>

        <blockquote className='blockquote'>
                <p className='text.center mt-3'>
                Los Platos tpicos de Potos son Cazuela, Aj de Pataskha, Chambergos, Chajchu, Calapurka entre otros que son caractersticos de la villa imperial. Sopa de man: Sopa en base a man molido, acompaada con papas, carne y pescado con arroz o fideo.
                </p>
            </blockquote>
            <h1 className='text-white p-2 bg-dark'>KALAPURKA</h1>
            <img src="./imagenes/FRITANGA.jpg" width= '80%' height= '315' alt="kalapurka" fluid />
            <p className='text.center mt-3'>
                    <h2>Ingredientes</h2>
                    <h3>1 kilogramo de Carne o charque de res</h3>
                    <h3>Piedras volcnicas</h3>
                    <h3>8 Papas variedad</h3>
                    <h3> libra de Harina</h3>
                    <h3> libra de Harina de maz blanco</h3>
                    <h3>1 Cebolla</h3>
                    <h3>2 Ajos</h3>
                    <h3>Aj colorado molido Organo, sal, comino y pimienta</h3>

                    <h1>Paso a paso</h1>

                    <h3>Paso 1 
                    Calentar las piedras volcnicas al fuego vivo, en brasas, empleando cocinas de las que se usan en parrillas.</h3>
                    <h3>Paso 2
                    Cocinar carne o charque, eventualmente empleando una olla a presin para acelerar el proceso. Suavizar la carne hasta que permita ser deshilada, y dejar reposar el preparado.</h3>
                    <h3>Paso 3
                    Frer en aceite el ajo y cebolla, aadir el aj, sal, comino y pimienta. Cocinar hasta que se seque el aceite, junto con el charque. Agregar las papas al charque as cocinado.</h3>
                    <h3>Paso 4 Mezclar las dos clases de harina y diluirlas con un poco de agua o caldo de la preparacin  previa. Verter este contenido a la olla cuando las papas estn hervidas.</h3>
                    <h3>Paso 5 Cuandoi se compruebe que todo est mezclado, apagar la cocina e introducir las piedras calientes en la olla.</h3>
                    <h3>Paso 6 Servir en platos de barro hondos, usualmente introduciendo tambin una piedra caliente a cada plato, al punto que la sopa espesa se presente como burbujeante. Agregar perejil u otra especia.  </h3>
                    
            </p>
            
            <h1 className='text-white p-2 bg-dark'>VIDEO PREPARACION</h1>
            <Container>
                    <Row>
                        <Col md={6} className="mx-auto mt-5">
                            <div className="embed-responsive embed-responsive-16by9">
                                <iframe
                                    width='80%'
                                    height='315'
                                    title="YouTube Video"
                                    className="embed-responsive-item"
                                    src={`https://www.youtube.com/embed/${videoId}`}
                                    allowFullScreen />
                            </div>
                        </Col>
                    </Row>
            </Container>



        <Footer/>
    </div>
  )
}

export default Potosi